import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import PeopleIcon from "@material-ui/icons/People";
import Chip from "@material-ui/core/Chip";

const mockPostData = ["Projeto1", "Projeto2", "Projeto3", "Projeto4"];

const Profile = () => {
  const classes = useStyles();

  const handleClick = () => {
    console.info("You clicked the Chip.");
  };

  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        <div className={classes.avatarDiv}>
          <Avatar
            alt="Remy Sharp"
            src="/static/images/avatar/1.jpg"
            className={classes.avatar}
          />
        </div>
        <Typography variant="h7" color="textSecondary" component="p">
          Maria Clara Dias
        </Typography>
        <Typography variant="caption" color="textSecondary" component="p">
          @mariaclaradias
        </Typography>

        <Typography variant="h7" color="textSecondary" component="p">
          <IconButton className={classes.icon} aria-label="add to favorites">
            <PeopleIcon />
          </IconButton>
          Alice Lara
        </Typography>
        <div className={classes.chipList}>
          {mockPostData.map((data) => {
            return (
              <Chip
                className={classes.chip}
                size="small"
                label={"#" + data}
                onClick={handleClick}
              />
            );
          })}
        </div>
      </Paper>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    overflow: "hidden",
    padding: "6vh 1vh",
    height: "100%",
    verticalAlign: "middle",
  },
  paper: {
    padding: theme.spacing(2),
    margin: "auto",
    maxWidth: 500,
  },
  avatar: {
    width: theme.spacing(15),
    height: theme.spacing(15),
  },
  avatarDiv: {
    alignItems: "center",
    display: "flex",
    justifyContent: "center",
    marginBottom: "2vh",
  },
  icon: {
    color: "#F58F6E",
  },
  chip: {
    backgroundColor: "#f7ac97",
    margin: "0.5vh 1vh 0.5vh 0",
  },
  chipList: {
    display: "flex",
    justifyContent: "left",
    flexWrap: "wrap",
    padding: "1vh 2vh 1vh 0",
  },
}));

export default Profile;
